.project-card {
  padding: 20px;
  cursor: pointer;
}

.flip-card {
  background-color: transparent;
  width: 250px;
  height: 250px;
  perspective: 1000px;
  padding: 10px;
}

.flip-card-inner {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: transform 0.6s;
  transform-style: preserve-3d;
  /* box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2); */
}

.flip-card:hover .flip-card-inner {
  transform: rotateY(180deg);
}

.flip-card-front,
.flip-card-back {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

.flip-card-front {
  /* background-color: #bbb; */
  background-color: white;
  color: black;
}

.flip-card-back {
  background-color: white;
  color: white;
  transform: rotateY(180deg);
  /* margin: 15px; */
}

.flip-card-back-content {
  padding: 10px;
}

.flip-img {
  width: 250px;
  height: 250px;
  background-color: white;
  /* border-radius: 100%; */
  border-radius: 20px;
}

.flip-name {
  font-weight: 400;
}

.flip-desc {
  font-weight: 200;
}

.project-title {
  font-size: calc(1rem + 0.5vw);
  font-weight: 200;
}
